/** Motiva Sans 200 **/

@font-face {
    font-family: "motiva-sans";
    src: url("motiva-sans-200/motiva-sans-200.woff2") format("woff2"), url("motiva-sans-200/motiva-sans-200.woff") format("woff"), url("motiva-sans-200/motiva-sans-200.otf") format("opentype");
    font-style: normal;
    font-weight: 200;
}

@font-face {
    font-family: "motiva-sans";
    src: url("motiva-sans-200-italic/motiva-sans-200-italic.woff2") format("woff2"), url("motiva-sans-200-italic/motiva-sans-200-italic.woff") format("woff"), url("motiva-sans-200-italic/motiva-sans-200-italic.woff") format("opentype");
    font-style: italic;
    font-weight: 200;
}


